<template>
    <div>
        <h2>用户添加信息</h2>
        <form action="">
            用户名：<input v-model="user.name" type="text"><br>
            年龄：<input v-model="user.age" type="text"><br>
            生日：<input v-model="user.bir" type="text"><br>
            <input type="text" @click="saveUserInfo" value="添加用户信息">
        </form>
    </div>
</template>

<script>
export default {
    name: 'UserAdd',
    data(){
        return{
            user:{},
        }
    },
    methods:{
        saveUserInfo(){
            console.log(this.user);
            //发送axios请求
            this.$http.post("http://localhost:8080/user/add",this.user).then(res=>{
                console.log(res);
                //响应结果成功
                if(res.data.success){
                    //导航到一个新的路由
                    this.$router.push("/user");
                }
            });
        }
    }
    
  }
</script>

<style></style>


